import { ApplicationConfig, Component, effect } from '@angular/core'
import { Router } from '@angular/router'
import { CommonModule } from '@angular/common'
import { IonicModule } from '@ionic/angular'
import { TranslateModule } from '@ngx-translate/core'
import { AppstateService } from 'src/app/services/internal/appstate.service'
import { StorageService } from 'src/app/services/utils/storage.service'
import {
  AbonnementWrapperService,
  CurrentAbonnementState,
} from 'src/app/services/api-wrappers/abonnement.service'
import { AnimationItem } from 'lottie-web'
import { LottieComponent, AnimationOptions } from 'ngx-lottie'

@Component({
  selector: 'app-abo-congrats',
  templateUrl: './abo-congrats.page.html',
  styleUrls: ['./abo-congrats.page.scss'],
  imports: [IonicModule, CommonModule, TranslateModule, LottieComponent],
})
export class AboCongratsPage {
  public programName: string
  public currentAbonnement: CurrentAbonnementState
  options: AnimationOptions = {
    path: '/assets/icon/animated/check.json',
  }

  constructor(
    private router: Router,
    private appStateService: AppstateService,
    private storage: StorageService,
    private AbonnementWrapperService: AbonnementWrapperService
  ) {
    effect(() => {
      this.currentAbonnement =
        this.AbonnementWrapperService.currentAbonnementState()
    })
  }

  async ionViewWillEnter() {
    await this.storage.get('program').then((prg) => {
      this.programName = prg?.name
    })
    setTimeout(() => {
      if (
        this.appStateService.getAppState().state === 'onboarding' ||
        this.appStateService.getAppState().state === 'launch'
      ) {
        this.appStateService.setAppState({
          state: 'new_cyclist',
          dring_state: {
            geolocation_dring: 'not_shown',
          },
          push_notifications: 'not_shown',
        })
        if (this.currentAbonnement.state == 'waiting_validation') {
          this.router.navigate(['/account'], { replaceUrl: true })
        } else {
          this.router.navigate(['/renting'], { replaceUrl: true })
        }
      } else {
        this.router.navigate(['/map'], { replaceUrl: true })
      }
    }, 5000)
  }
}
